import React, {useEffect, useState} from "react";
import {Button, Form, FormControl, Nav, Navbar,CloseButton} from "react-bootstrap";
import {Link} from "react-router-dom";

const NaviBar = (props) => {

    return (
        <>
            <Navbar  fixed="top" collapseOnSelect expand='md' bg='dark' variant='dark' className='p-2'>
                <Navbar.Brand><Link to='/'
                                    style={{textDecoration: 'none', color:"white", hover:{color:"yellow"}}}>DAGCAFE</Link></Navbar.Brand>
                <Navbar.Toggle aria-controls="responsive-navbar-nav"/>
                <Navbar.Collapse id='responsive-navbar-nav'>
                    <Nav className="me-2 my-1 my-lg-0 "
                         style={{ maxHeight: '16rem' }}>
                        <Nav.Link> <Link to='/'
                                         style={{textDecoration: 'none'}}>Главная</Link></Nav.Link>
                        <Nav.Link> <Link to='discounts' style={{textDecoration: 'none'}}>Акции</Link></Nav.Link>
                        <Nav.Link> <Link to='contacts' style={{textDecoration: 'none'}}>Контакты
                            сайта</Link>
                        </Nav.Link>
                        <Nav.Link> <Link to='addcafe' style={{textDecoration: 'none'}}>Управление
                            </Link>
                        </Nav.Link>
                    </Nav>

                </Navbar.Collapse>
            </Navbar>
        </>
    )
}

export default NaviBar