import React from "react";
import {Card, Button, Carousel, Container} from "react-bootstrap";
import AboutCafe from "./AboutCafe";


const DiscountsList = (props) => {
    const value = props.value


    return (
        < >
                <Card className='bg-primary text-white text-center' style={{borderRadius:0}}>
                <Card.Header>
                    <h2>{value.title}</h2>
                </Card.Header>
            </Card>
                <Carousel>
                    {Object.values(value.discounts).map(value =>
                        <Carousel.Item>
                            <img
                                className="d-block w-100 "
                                src={value.img}
                            />
                            <Carousel.Caption>
                                <Card className=" text-white" style={{backgroundColor: "#3095ed45", borderRadius:"30px"}}>
                                    <Card.Body>
                                        <h2>{value.name}</h2>
                                        <h3>{value.date}</h3>
                                        <p>{value.description}</p>
                                        <AboutCafe value={props.value}/>
                                    </Card.Body>
                                </Card>
                            </Carousel.Caption>
                        </Carousel.Item>
                    )}
                </Carousel>
        </>
    )
}

export default DiscountsList