import React, {useState} from "react";
import {Badge, Button, Card, Container, Image, Offcanvas} from "react-bootstrap";

const AboutCafe = (props) => {
    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const toggleShow = () => setShow((s) => !s);
    const date = new Date
    return (
        <>
            <Button variant="info" onClick={toggleShow} style={{width: "100%"}}>
                <h5>Открыть заведение</h5>
            </Button>
            <Offcanvas show={show} onHide={handleClose}>
                <Offcanvas.Header closeButton>
                    <Offcanvas.Title>{props.value.title}</Offcanvas.Title>
                </Offcanvas.Header>
                <Image src={props.value.img} roundedCircle/>
                <hr/>
                <Card>
                    <Card.Header style={{fontSize: "25px"}}>

                        Время
                        работы {(date.getHours() >= props.value.worktime.from && date.getHours() < props.value.worktime.to) ?
                        <Badge bg="success">{props.value.worktime.from}:00 - {props.value.worktime.to}:00</Badge> :
                        <Badge bg="danger">{props.value.worktime.from}:00 - {props.value.worktime.to}:00</Badge>
                    }
                    </Card.Header>
                    <Card.Body  style={{fontSize: "35px"}}>
                        <Button className='m-auto overflow-hidden'>{props.value.number}</Button>
                        <div>
                        <Badge bg="info" type='addres' >{props.value.address}</Badge>
                        </div>
                    </Card.Body>
                </Card>
                <Offcanvas.Body>
                    {props.value.description.text}
                </Offcanvas.Body>
            </Offcanvas>
        </>
    );
}

export default AboutCafe